<template>
<el-table
    :data="journalList.filter(data => !search || data.book.name.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%">
    <el-table-column
        label="Книга"
        prop="book.name">
    </el-table-column>
    <el-table-column
        label="Клиент"
        prop="client.lastName">
    </el-table-column>
    <el-table-column
        label="Дата начала"
        prop="dateBegin">
    </el-table-column>
    <el-table-column
        label="Дата конца"
        prop="dateEnd">
    </el-table-column>
  <el-table-column
      label="Дата возврата"
      prop="dateReturn">
    </el-table-column>
    <el-table-column align="right">
      <template slot="header" slot-scope="scope">
        <el-input
            v-model="search"
            size="mini"
            placeholder="Поиск по книге"/>
      </template>
      <template slot-scope="scope">
          <div class="buttons">
              <el-popover
                  @show="setShown"
                  @hide="setHide"
                  width="300"
                  trigger="click">
                  <JournalForm :journal="scope.row" @handleSubmit="onChange" :books="books" :clients="clients"
                               v-if="shown"/>
            <el-button size="mini" slot="reference" type="primary">Изменить</el-button>
        </el-popover>
        <el-button
            size="mini"
            type="danger"
            @click="onDelete(scope.row.id)">Удалить</el-button>
          </div>
      </template>
    </el-table-column>
</el-table>
</template>

<script>
import ClientForm from "@/components/Clients/ClientForm";
import JournalForm from "@/components/Journal/JournalForm";

export default {
  name: "JournalTable",
  components: { JournalForm, ClientForm },
  props: {
    journalList: {
      type: Array,
      default: () => []
    },
    books: {
      type: Array,
      default: () => ({})
    },
    clients: {
      type: Array,
      default: () => ({})
    }
  },
  data: () => ({
    search: '',
    shown: false
  }),
  methods: {
    onDelete(id) {
      this.$emit("handleDelete", id);
    },
    onChange(obj) {
      this.$emit('handleChange', obj);
    },
    setShown() {
      this.shown = true;
    },
    setHide() {
      this.shown = false;
    }
  }
}
</script>

<style scoped>
.buttons {
    display: grid;
    gap: 0.5em;
    justify-content: left;
}
</style>
