<template>
    <el-form :model="bookTypeModel" status-icon :rules="rules" ref="form" class="form">
            <el-form-item label="Название" prop="name">
                <el-input type="text" v-model="bookTypeModel.name" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Количество" prop="count">
                <el-input v-model.number="bookTypeModel.count" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Штраф" prop="fine">
                <el-input v-model.number="bookTypeModel.fine" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Количество дней" prop="dayCount">
                <el-input v-model.number="bookTypeModel.dayCount" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary"
                           @click="onSubmit">{{bookType.name.length ? `Изменить` : `Добавить`}}</el-button>
            </el-form-item>
        </el-form>
</template>

<script>
export default {
  name: "BookTypeForm",
  props: {
    bookType: {
      type: Object,
      default: () => ({
        name: '',
        count: '',
        fine: '',
        dayCount: '',
      })
    }
  },
  data: () => ({
    bookTypeModel: {
      name: '',
      count: '',
      fine: '',
      dayCount: '',
    },
    rules: {
      name: [
        { required: true, message: "Введите название", trigger: 'blur' },
      ],
      count: [
        { required: true, message: "Введите количество", trigger: 'blur' },
        { type: 'number', message: "Количество должно быть числом", trigger: ['change', 'blur'] },
        // { min: 1, message: 'Количество должно быть больше 0', trigger: 'blur' }
      ],
      fine: [
        { required: true, message: "Введите штраф", trigger: 'blur' },
        { type: 'number', message: "Штраф должен быть числом", trigger: ['change', 'blur'] },
        // { min: 1,  message: 'Штраф должен быть больше 0', trigger: 'blur' }
      ],
      dayCount: [
        { required: true, message: "Введите количество дней", trigger: 'blur' },
        { type: 'number', message: "Количество дней должно быть числом", trigger: ['change', 'blur'] },
        // { min: 1, message: 'Количество дней должно быть больше 0', trigger: 'blur' }
      ]
    }
  }),
  mounted() {
    this.bookTypeModel = { ...this.bookType };
  },
  methods: {
    onSubmit() {
      const form = this.$refs.form;
      form.validate((valid) => {
        if (valid) {
          const obj = { ...this.bookTypeModel };
          this.$emit('handleSubmit', obj);
          form.resetFields();
        }
      })
    }
  }
}
</script>

