import Vue from 'vue'
import VueRouter from 'vue-router'
import BookTypeDisplayer from "@/components/BookType/BookTypeDisplayer";
import BookDisplayer from "@/components/Book/BookDisplayer";
import ClientDisplayer from "@/components/Clients/ClientDisplayer";
import JournalDisplayer from "@/components/Journal/JournalDisplayer";
import NotFound from "@/components/NotFound";
import { isAuthenticated } from '@/helpers/authHelpers.js'
import Login from "@/components/Auth/Login";
import Registration from "@/components/Auth/Registration";
import ro from "element-ui/src/locale/lang/ro";

Vue.use(VueRouter)

const routes = [
  { name: 'bookTypes', path: '/bookTypes', component: BookTypeDisplayer },
  { name: 'books', path: '/books', component: BookDisplayer },
  { name: 'clients', path: '/clients', component: ClientDisplayer },
  { name: 'journals', path: '/journals', component: JournalDisplayer },
  { name: 'login', path: '/login', component: Login },
  { name: 'registration', path: '/registration', component: Registration },
  { path: '*', name: 'default', component: NotFound }
]

const router = new VueRouter({
  mode: 'history',
  base: '/',
  routes,
});

setUpRouter(router);

function setUpRouter(router) {
  router.beforeEach((to, from, next) => {
    if (authOnly(to) && !isAuthenticated()) {
      next({ name: 'login' })
    } else {
      next();
    }
  });

  router.beforeEach((to, from, next) => {
    if (notAuthOnly(to) && isAuthenticated()) {
      next({ name: 'bookTypes' })
    } else {
      next();
    }
  });
}

function authOnly(route) {
  return (
    route.name === 'bookTypes' ||
    route.name === 'books' ||
    route.name === 'clients' ||
    route.name === 'journals' ||
    route.name === 'default'
  )
}

function notAuthOnly(route) {
  return (
    route.name === 'login' ||
    route.name === 'registration'
  )
}

export default router;
