<template>
    <el-tabs type="border-card" class="center">
    <el-tab-pane label="Список">
        <template v-if="this.clients.length">
            <ClientTable :clientList="clients" @handleDelete="onDelete" @handleChange="onChange"/>
        </template>
        <template v-else>
            Нет данных
        </template>
    </el-tab-pane>
    <el-tab-pane label="Добавить">
        <ClientForm @handleSubmit="onAdd" class="formPadding"/>
    </el-tab-pane>
</el-tabs>
</template>

<script>
import BookTypeForm from "@/components/BookType/BookTypeForm";
import BookTypeTable from "@/components/BookType/BookTypeTable";
import { mapActions, mapGetters } from "vuex";
import ClientTable from "@/components/Clients/ClientTable";
import ClientForm from "@/components/Clients/ClientForm";

export default {
  name: "ClientDisplayer",
  components: { ClientForm, ClientTable, BookTypeForm, BookTypeTable },
  computed: {
    ...mapGetters('clients', ['clients'])
  },
  methods: {
    ...mapActions('clients', ['addClient', 'putClient', 'deleteClient']),
    ...mapActions('journals', ['fetchJournals']),
    onDelete(id) {
      this.$confirm('Клиент будет удален навсегда. Продолжить?', 'Предупреждение', {
        confirmButtonText: 'Ок',
        cancelButtonText: 'Отменить',
        type: 'warning'
      }).then(async () => {
        try {
          await this.deleteClient(id);
          this.$message({
            type: 'success',
            message: 'Клиент успешно удален'
          });
        } catch (e) {
          this.$message({
            type: 'error',
            message: 'Неудачная попытка удаления'
          })
        }
      }).catch(() => {
      });
    },
    async onAdd(client) {
      try {
        await this.addClient(client);
        this.$message({
          type: 'success',
          message: 'Клиент успешно добавлен'
        })
      } catch (e) {
        this.$message({
          type: 'error',
          message: 'Неудачная попытка добавления'
        })
      }
    },
    async onChange(client) {
      try {
        await this.putClient({ id: client.id, client });
        await this.fetchJournals();
        this.$message({
          type: 'success',
          message: 'Клиент успешно изменен'
        })
      } catch (e) {
        this.$message({
          type: 'error',
          message: 'Неудачная попытка изменения'
        })
      }
    }
  }
}
</script>

