<template>
    <el-card class="center">
        <div slot="header" class="clearfix">
    <span>Регистрация</span>
    <router-link :to="{name: 'login'}">
                <el-button style="float: right; padding: 3px 0" type="text">Войти</el-button>
            </router-link>
  </div>
        <el-form ref="form" :model="authRequest" :rules="rules">

  <el-form-item label="Имя пользователя" prop="userName">
    <el-input v-model="authRequest.userName"></el-input>
  </el-form-item>

<el-form-item label="Пароль" prop="password">
    <el-input type="password" v-model="authRequest.password" ref="password"></el-input>
</el-form-item>
  <el-form-item>

    <el-button type="primary" @click="onSubmit">Зарегистрироваться</el-button>
  </el-form-item>
</el-form>
    </el-card>
</template>

<script>
import { mapActions } from "vuex";
import userApiService from "@/services/userApiService";

export default {
  name: "Registration",
  data: () => ({
    authRequest: {
      userName: '',
      password: '',
    },
    rules: {
      userName: [
        {
          required: true, message: 'Введите имя пользователя', target: 'blur'
        }
      ],
      password: [
        {
          required: true, message: 'Введите пароль', target: 'blur'
        }
      ]
    }
  }),
  methods: {
    ...mapActions('users', ['login']),
    async onSubmit() {
      const form = this.$refs.form;

      form.validate(validate.bind(this));
    }
  }
}

async function validate(validate) {
  try {
    if (validate) {
      await userApiService.signUp(this.authRequest);
      this.$message({
        type: 'success',
        message: 'Пользователь успешно зарегистрирован'
      });
    }
  } catch (e) {
    if (e === 422) {
      this.$message({
        type: 'error',
        message: 'Пользователь с таким именим уже существует'
      });
    } else {
      this.$message({
        type: 'error',
        message: 'Ошибка на сервере'
      });
    }
  } finally {
    this.$refs.form.resetFields();
  }
}
</script>

<style scoped>
.center {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 800px;
    margin: auto;
}
</style>
