<template>

    <el-table
        :data="bookList.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
    <el-table-column
        label="Название"
        prop="name">
    </el-table-column>
    <el-table-column
        label="Количество"
        prop="count">
    </el-table-column>
    <el-table-column
        label="Тип книги"
        prop="bookType.name">
    </el-table-column>
    <el-table-column align="right">

      <template slot="header" slot-scope="scope">
        <el-input
            v-model="search"
            size="mini"
            placeholder="Поиск по названию"/>
      </template>
      <template slot-scope="scope">
          <div class="buttons">
              <el-popover
                  @show="setShown"
                  @hide="setHide"
                  width="300"
                  trigger="click">
                  <BookForm :book="scope.row" :bookTypes="bookTypes" @handleSubmit="onChange" v-if="shown"/>
            <el-button size="mini" slot="reference" type="primary">Изменить</el-button>
        </el-popover>
        <el-button
            size="mini"
            type="danger"
            @click="onDelete(scope.row.id)">Удалить</el-button>
          </div>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import BookForm from "@/components/Book/BookForm";

export default {
  name: "BookTable",
  components: { BookForm },
  props: {
    bookList: {
      type: Array,
      default: () => []
    },
    bookTypes: {
      type: Array,
      default: () => []
    }
  },
  data: () => ({
    search: '',
    shown: false
  }),
  methods: {
    onDelete(id) {
      this.$emit("handleDelete", id);
    },
    onChange(obj) {
      this.$emit('handleChange', obj);
    },
    setShown() {
      this.shown = true;
    },
    setHide() {
      this.shown = false;
    }
  }
}
</script>

<style scoped>
    .buttons {
        display: flex;
        justify-content: space-between;
    }
</style>
