<template>
    <el-table
        :data="bookTypeList.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
    <el-table-column
        label="Название"
        prop="name">
    </el-table-column>
    <el-table-column
        label="Количество"
        prop="count">
    </el-table-column>
    <el-table-column
        label="Штраф"
        prop="fine">
    </el-table-column>
    <el-table-column
        label="Количество дней"
        prop="dayCount">
    </el-table-column>

    <el-table-column align="right">
      <template slot="header" slot-scope="scope">
        <el-input
            v-model="search"
            size="mini"
            placeholder="Поиск по названию"/>
      </template>
      <template slot-scope="scope">
          <div class="buttons">
              <el-popover
                  @show="setShown"
                  @hide="setHide"
                  width="300"
                  trigger="click">
              <BookTypeForm :bookType="scope.row" v-if="shown" @handleSubmit="onChange"/>
            <el-button size="mini" slot="reference" type="primary">Изменить</el-button>
        </el-popover>
        <el-button
            size="mini"
            type="danger"
            @click="onDelete(scope.row.id)">Удалить</el-button>
          </div>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import BookTypeForm from "@/components/BookType/BookTypeForm";

export default {
  name: "BookTypeTable",
  components: { BookTypeForm },
  props: {
    bookTypeList: {
      type: Array,
      default: () => []
    }
  },
  data: () => ({
    search: '',
    shown: false
  }),
  methods: {
    onDelete(id) {
      this.$emit("handleDelete", id);
    },
    onChange(obj) {
      this.$emit('handleChange', obj);
    },
    setShown() {
      this.shown = true;
    },
    setHide() {
      this.shown = false;
    }
  }
}
</script>

<style scoped>

    .buttons {
        display: flex;
        justify-content: space-between;
    }
</style>
