import journalApiService from "@/services/journalApiService";
import mutations from "@/store/mutations";
import Vue from "vue";
import { arrayToObj } from "@/helpers/storeHelpers";

const { SET, SET_ONE, DELETE } = mutations;

const journalStore = {
  namespaced: true,
  state: {
    journals: {}
  },
  getters: {
    journals(state) {
      return Object.values(state.journals);
    }
  },
  mutations: {
    [SET](state, objList) {
      state.journals = objList;
    },
    [SET_ONE](state, obj) {
      Vue.set(state.journals, obj.id, obj);
    },
    [DELETE](state, id) {
      Vue.delete(state.journals, id);
    }
  },
  actions: {
    async fetchJournals({ commit }) {
      try {
        const response = await journalApiService.getAll();

        commit(SET, arrayToObj(response));
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async addJournal({ commit }, journal) {
      try {
        const response = await journalApiService.post(journal);

        commit(SET_ONE, response);
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async putJournal({ commit }, { id, journal }) {
      try {
        const response = await journalApiService.put(id, journal);

        commit(SET_ONE, response);
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async deleteJournal({ commit }, id) {
      try {
        await journalApiService.delete(id);

        commit(DELETE, id);
      } catch (e) {
        return Promise.reject(e)
      }
    }
  },
};

export default journalStore;
