<template>
<el-form :model="journalModel" status-icon :rules="rules" ref="form" class="form right">
            <el-form-item label="Книги" prop="book">
                <el-select v-model="journalModel.book">
                <el-option v-for="bookObj in this.books"
                           :key=" bookObj.id"
                           :value="bookObj"
                           :label="journalModel.book.name">
                    {{ bookObj.name }}
                </el-option>
            </el-select>
            </el-form-item>
            <el-form-item label="Клиент" prop="client">
                <el-select v-model="journalModel.client">
                <el-option v-for="clientOvj in this.clients"
                           :key="clientOvj.id"
                           :value="clientOvj"
                           :label="clientName">
                    {{ `${clientOvj.lastName} ${clientOvj.firstName} ${clientOvj.patherName}` }}
                </el-option>
            </el-select>
            </el-form-item>
            <el-form-item label="Дата начала" prop="dateBegin">
                <el-date-picker
                    v-model="journalModel.dateBegin"
                    type="date"
                    placeholder="Pick a day">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="Дата конца" prop="dateEnd">
                <el-date-picker
                    v-model="journalModel.dateEnd"
                    type="date"
                    placeholder="Pick a day">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="Дата возврата" prop="dateReturn">
                <el-date-picker
                    v-model="journalModel.dateReturn"
                    type="date"
                    placeholder="Pick a day">
                </el-date-picker>
            </el-form-item>
            <el-form-item class="btn">
                <el-button type="primary"
                           @click="onSubmit">{{journal.dateBegin.length ? `Изменить` : `Добавить`}}</el-button>
            </el-form-item>
        </el-form>
</template>

<script>
export default {
  name: "JournalForm",
  props: {
    journal: {
      type: Object,
      default: () => ({
        book: {
          name: ''
        },
        client: {
          firstName: '',
          lastName: '',
          patherName: '',
        },
        dateBegin: '',
        dateEnd: '',
        dateReturn: '',
      })
    },
    books: {
      type: Array,
      default: () => ({})
    },
    clients: {
      type: Array,
      default: () => ({})
    }
  },
  data: () => ({
    journalModel: {
      book: {
        name: ''
      },
      client: {
        firstName: '',
        lastName: '',
        patherName: '',
      },
      dateBegin: '',
      dateEnd: '',
      dateReturn: '',
    },
    rules: {
      book: [
        { validator: bookValidation, message: "Выберите книгу", trigger: 'change' },
      ],
      client: [
        { validator: clientValidation, message: "Выберите клиента", trigger: 'change' },
      ],
      dateBegin: [
        { required: true, message: "Укажите дату начала", trigger: 'blur' },
      ],
      dateEnd: [
        { required: true, message: "Укажите дату конца", trigger: 'blur' },
      ],
      dateReturn: [
        { required: true, message: "Укажите дату возврата", trigger: 'blur' },
      ]
    }
  }),
  computed: {
    clientName() {
      if (!this.journalModel.client.lastName.length) {
        return '';
      }

      return `${ this.journalModel.client.lastName } ${ this.journalModel.client.firstName } ${ this.journalModel.client.patherName }`;
    }
  },
  mounted() {
    this.journalModel = { ...this.journal };
  },
  methods: {
    onSubmit() {
      const form = this.$refs.form;
      form.validate((valid) => {
        if (valid) {
          const obj = { ...this.journalModel };
          this.$emit('handleSubmit', obj);
          form.resetFields();
        }
      })
    }
  }
}

let bookValidation = (rule, value, callback) => {
  if (!value.name.length) {
    callback(new Error('Необходимо выбрать тип книги'));
  } else {
    callback();
  }
};

let clientValidation = (rule, value, callback) => {
  if (!value.firstName.length) {
    callback(new Error('Необходимо выбрать тип книги'));
  } else {
    callback();
  }
};
</script>

<style scoped>
.right > :not(.btn) {
    text-align: right;
}
</style>
