<template>
<el-form :model="clientModel" status-icon :rules="rules" ref="form" class="form">
            <el-form-item label="Имя" prop="firstName">
                <el-input type="text" v-model="clientModel.firstName" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Фамилия" prop="lastName">
                <el-input v-model.number="clientModel.lastName" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Отчество" prop="patherName">
                <el-input v-model.number="clientModel.patherName" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Серия паспорта" prop="passportSeria">
                <el-input v-model.number="clientModel.passportSeria" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Номер паспорта" prop="passportNum">
                <el-input v-model.number="clientModel.passportNum" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary"
                           @click="onSubmit">{{client.firstName.length ? `Изменить` : `Добавить`}}</el-button>
            </el-form-item>
        </el-form>
</template>

<script>
export default {
  name: "ClientForm",
  props: {
    client: {
      type: Object,
      default: () => ({
        firstName: '',
        lastName: '',
        patherName: '',
        passportSeria: '',
        passportNum: '',
      })
    }
  },
  data: () => ({
    clientModel: {
      firstName: '',
      lastName: '',
      patherName: '',
      passportSeria: '',
      passportNum: '',
    },
    rules: {
      firstName: [
        { required: true, message: "Введите имя", trigger: 'blur' },
      ],
      lastName: [
        { required: true, message: "Введите фамилию", trigger: 'blur' },
      ],
      patherName: [
        { required: true, message: "Введите отчество", trigger: 'blur' },
      ],
      passportSeria: [
        { required: true, message: "Введите серию паспорта", trigger: 'blur' },
      ],
      passportNum: [
        { required: true, message: "Введите номер паспорта", trigger: 'blur' },
      ]
    }
  }),
  mounted() {
    this.clientModel = { ...this.client };
  },
  methods: {
    onSubmit() {
      const form = this.$refs.form;
      form.validate((valid) => {
        if (valid) {
          const obj = { ...this.clientModel };
          this.$emit('handleSubmit', obj);
          form.resetFields();
        }
      })
    }
  }
}
</script>

