<template>
<el-form :model="bookModel" status-icon :rules="rules" ref="form" class="form">
            <el-form-item label="Название" prop="name">
                <el-input type="text" v-model="bookModel.name" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Количество" prop="count">
                <el-input v-model.number="bookModel.count" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="Тип книги" prop="bookType">
                <el-select v-model="bookModel.bookType">
                <el-option v-for="bookTypeObj in this.bookTypes"
                           :key="bookTypeObj.id"
                           :value="bookTypeObj"
                           :label="bookModel.bookType.name">
                    {{ bookTypeObj.name }}
                </el-option>
            </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary"
                           @click="onSubmit">{{book.name.length ? `Изменить` : `Добавить`}}</el-button>
            </el-form-item>
</el-form>
</template>

<script>
export default {
  name: "BookForm",
  props: {
    book: {
      type: Object,
      default: () => ({
        name: '',
        count: '',
        bookType: {
          name: ''
        }
      })
    },
    bookTypes: {
      type: Array,
      required: true
    }
  },
  data: () => ({
    bookModel: {
      name: '',
      count: '',
      bookType: {
        name: ''
      }
    },
    rules: {
      name: [
        { required: true, message: "Введите название", trigger: 'blur' },
      ],
      count: [
        { required: true, message: "Введите количество", trigger: 'blur' },
        { type: 'number', message: "Количество должно быть числом", trigger: ['change', 'blur'] },

      ],
      bookType: [
        { validator: selectValidation, trigger: 'blur' }
      ]
    }
  }),
  mounted() {
    this.bookModel = { ...this.book };
  },
  methods: {
    onSubmit() {
      const form = this.$refs.form;
      form.validate((valid) => {
        if (valid) {
          const obj = { ...this.bookModel };
          this.$emit('handleSubmit', obj);
          form.resetFields();
        }
      })
    }
  }
}

let selectValidation = (rule, value, callback) => {
  if (!value.name.length) {
    callback(new Error('Необходимо выбрать тип книги'));
  } else {
    callback();
  }
};
</script>

