import mutations from "@/store/mutations";
import userApiService from "@/services/userApiService";
import CONST_ENV from "@/config/const.config";

const { ADD_USER } = mutations;

const userStore = {
  namespaced: true,
  state: {
    user: null
  },
  getters: {
    usersList: ({ list }) => Object.values(list)
  },
  mutations: {
    [ADD_USER](state, user) {
      state.user = user;
    }
  },
  actions: {
    async login({ commit }, user) {
      const { userName, token } = await userApiService.signIn(user);

      localStorage.setItem(CONST_ENV.TOKEN_KEY, token);

      commit(ADD_USER, userName);
    }
  }
}

export default userStore;
