import axios from '../plugins/axios';

class ClientApiService {
  async getAll() {
    try {
      const result = await axios.get("/api/clients/all");

      return result.data;
    } catch (e) {
      return Promise.reject(e);
    }
  }

  async post(body) {
    try {
      const result = await axios.post(`/api/clients/add`, body);

      return result.data
    } catch (e) {
      return Promise.reject(e);
    }
  }

  async put(id, body) {
    try {
      const result = await axios.put(`/api/clients/${ id }`, body);

      return result.data;
    } catch (e) {
      return Promise.reject(e);
    }
  }

  async delete(id) {
    try {
      await axios.delete(`/api/clients/${ id }`);

    } catch (e) {
      return Promise.reject(e);
    }
  }
}

const clientApiService = new ClientApiService();

export default clientApiService;
