import axios from '../plugins/axios';

class BookTypeSpiService {
  async getAll() {
    try {
      const result = await axios.get("/api/types/all");

      return result.data;
    } catch (e) {
      return Promise.reject(e);
    }
  }

  async post(body) {
    try {
      const result = await axios.post(`/api/types/add`, body);

      return result.data
    } catch (e) {
      return Promise.reject(e);
    }
  }

  async put(id, body) {
    try {
      const result = await axios.put(`/api/types/${ id }`, body);

      return result.data;
    } catch (e) {
      return Promise.reject(e);
    }
  }

  async delete(id) {
    try {
      await axios.delete(`/api/types/${ id }`);

    } catch (e) {
      return Promise.reject(e);
    }
  }
}

const bookTypeApiService = new BookTypeSpiService();

export default bookTypeApiService;
