import CONST_ENV from "@/config/const.config";

export default function (axios) {
  axios.interceptors.request.use(pasteApplicationJsonHeader);
  axios.interceptors.request.use(pasteToken)
}

function pasteApplicationJsonHeader(config) {
  if (config.method === 'post' || config.method === 'put') {
    config.headers['Content-Type'] = 'application/json';
  }
  return config;
}

function pasteToken(config) {
  const token = localStorage.getItem(CONST_ENV.TOKEN_KEY);
  if (token) {
    config.headers['Authorization'] = 'Bearer ' + token;
  }

  return config;
}
