<template>
    <el-card class="center">
        <div slot="header" class="clearfix">
    <span>Авторизация</span>
            <router-link :to="{name: 'registration'}">
                <el-button style="float: right; padding: 3px 0" type="text">Регистрация</el-button>
            </router-link>
  </div>
        <el-form ref="form" :model="authRequest" :rules="rules">
  <el-form-item label="Имя пользователя" prop="userName">
    <el-input v-model="authRequest.userName"></el-input>
  </el-form-item>
<el-form-item label="Пароль" prop="password">
    <el-input type="password" v-model="authRequest.password"></el-input>
  </el-form-item>

  <el-form-item>
    <el-button type="primary" @click="onSubmit">Войти</el-button>
  </el-form-item>
</el-form>
    </el-card>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: "Login",
  data: () => ({
    authRequest: {
      userName: '',
      password: ''
    },
    rules: {
      userName: [
        {
          required: true, message: 'Введите имя пользователя', target: 'blur'
        }
      ],
      password: [
        {
          required: true, message: 'Введите пароль', target: 'blur'
        }
      ]
    }
  }),
  methods: {
    ...mapActions('users', ['login']),
    async onSubmit() {
      const form = this.$refs.form;

      form.validate(validate.bind(this));
    }
  }
}

async function validate(validate) {
  try {
    if (validate) {
      await this.login(this.authRequest);
      location.pathname = '/bookTypes';
    }
  } catch (e) {
    this.$message({
      type: 'error',
      message: 'Неудачная попытка входа'
    });
  } finally {
    this.$refs.form.resetFields();
  }
}
</script>

<style scoped>
.center {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 800px;
    margin: auto;
}
</style>
