import mutations from "@/store/mutations";
import Vue from "vue";
import bookApiService from "@/services/bookApiService";
import { arrayToObj } from "@/helpers/storeHelpers";

const { SET, SET_ONE, DELETE } = mutations;

const bookStore = {
  namespaced: true,
  state: {
    books: {}
  },
  getters: {
    books(state) {
      return Object.values(state.books);
    }
  },
  mutations: {
    [SET](state, objList) {
      state.books = objList;
    },
    [SET_ONE](state, obj) {
      Vue.set(state.books, obj.id, obj);
    },
    [DELETE](state, id) {
      Vue.delete(state.books, id);
    }
  },
  actions: {
    async fetchBooks({ commit }) {
      try {
        const response = await bookApiService.getAll();

        commit(SET, arrayToObj(response));
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async addBook({ commit }, book) {
      try {
        const response = await bookApiService.post(book);

        commit(SET_ONE, response);
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async putBook({ commit }, {id, book}) {
      try {
        const response = await bookApiService.put(id, book);

        commit(SET_ONE, response);
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async deleteBook({ commit }, id) {
      try {
        await bookApiService.delete(id);

        commit(DELETE, id);
      } catch (e) {
        return Promise.reject(e)
      }
    }
  },
}

export default bookStore;
