import clientApiService from "@/services/clientApiService";
import mutations from "@/store/mutations";
import Vue from "vue";
import { arrayToObj } from "@/helpers/storeHelpers";

const { SET, SET_ONE, DELETE } = mutations;

const clientStore = {
  namespaced: true,
  state: {
    clients: {}
  },
  getters: {
    clients(state) {
      return Object.values(state.clients);
    }
  },
  mutations: {
    [SET](state, objList) {
      state.clients = objList;
    },
    [SET_ONE](state, obj) {
      Vue.set(state.clients, obj.id, obj);
    },
    [DELETE](state, id) {
      Vue.delete(state.clients, id);
    }
  },
  actions: {
    async fetchClients({ commit }) {
      try {
        const response = await clientApiService.getAll();

        commit(SET, arrayToObj(response));
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async addClient({ commit }, client) {
      try {
        const response = await clientApiService.post(client);

        commit(SET_ONE, response);
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async putClient({ commit }, { id, client }) {
      try {
        const response = await clientApiService.put(id, client);

        commit(SET_ONE, response);
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async deleteClient({ commit }, id) {
      try {
        await clientApiService.delete(id);

        commit(DELETE, id);
      } catch (e) {
        return Promise.reject(e)
      }
    }
  },
};

export default clientStore;
