import axios from '../plugins/axios';

class BookApiService {
  async getAll() {
    try {
      const result = await axios.get("/api/books/all");

      return result.data;
    } catch (e) {
      return Promise.reject(e);
    }
  }

  async post(body) {
    try {
      const result = await axios.post(`/api/books/add`, body);

      return result.data
    } catch (e) {
      return Promise.reject(e);
    }
  }

  async put(id, body) {
    try {
      const result = await axios.put(`/api/books/${ id }`, body);

      return result.data;
    } catch (e) {
      return Promise.reject(e);
    }
  }

  async delete(id) {
    try {
      await axios.delete(`/api/books/${ id }`);

    } catch (e) {
      return Promise.reject(e);
    }
  }
}

const bookApiService = new BookApiService();

export default bookApiService;
