<template>
<el-table
    :data="clientList.filter(data => !search || data.firstName.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%">
    <el-table-column
        label="Имя"
        prop="firstName">
    </el-table-column>
    <el-table-column
        label="Фамилия"
        prop="lastName">
    </el-table-column>
    <el-table-column
        label="Отчество"
        prop="patherName">
    </el-table-column>
    <el-table-column
        label="Серия паспорта"
        prop="passportSeria">
    </el-table-column>
  <el-table-column
      label="Номер паспорта"
      prop="passportNum">
    </el-table-column>
    <el-table-column align="right">
      <template slot="header" slot-scope="scope">
        <el-input
            v-model="search"
            size="mini"
            placeholder="Поиск по имени"/>
      </template>
      <template slot-scope="scope">
          <div class="buttons">
              <el-popover
                  @show="setShown"
                  @hide="setHide"
                  width="300"
                  trigger="click">
                  <ClientForm :client="scope.row" @handleSubmit="onChange" v-if="shown"/>
            <el-button size="mini" slot="reference" type="primary">Изменить</el-button>
        </el-popover>
        <el-button
            size="mini"
            type="danger"
            @click="onDelete(scope.row.id)">Удалить</el-button>
          </div>
      </template>
    </el-table-column>
</el-table>
</template>

<script>
import ClientForm from "@/components/Clients/ClientForm";
export default {
  name: "ClientTable",
  components: { ClientForm },
  props: {
    clientList: {
      type: Array,
      default: () => []
    }
  },
  data: () => ({
    search: '',
    shown: false
  }),
  methods: {
    onDelete(id) {
      this.$emit("handleDelete", id);
    },
    onChange(obj) {
      this.$emit('handleChange', obj);
    },
    setShown() {
      this.shown = true;
    },
    setHide() {
      this.shown = false;
    }
  }
}
</script>

<style scoped>
.buttons {
    display: grid;
    gap: 0.5em;
    justify-content: left;
}
</style>
