<template>
   <div>
       <a href="/logout" v-if="authenticated">
           <el-button type="danger" @click="handleClick">Выйти</el-button>
       </a>
   </div>
</template>

<script>
import CONST_ENV from "@/config/const.config";

export default {
  name: "AuthButtons",
  props: {
    authenticated: {
      Type: Boolean,
      required: true
    }
  },
  methods: {
    handleClick() {
      localStorage.removeItem(CONST_ENV.TOKEN_KEY);
    }
  }
}
</script>

