<template>
    <el-tabs type="border-card" class="center">
    <el-tab-pane label="Список">
        <template v-if="this.journals.length">
            <JournalTable :journalList="journals"  :clients="clients" :books="books" @handleChange="onChange" @handleDelete="onDelete"/>
        </template>
        <template v-else>
            Нет данных
        </template>
    </el-tab-pane>
    <el-tab-pane label="Добавить">
        <JournalForm :clients="clients" :books="books" @handleSubmit="onAdd" class="formPadding"/>
    </el-tab-pane>
</el-tabs>
</template>

<script>
import BookTypeForm from "@/components/BookType/BookTypeForm";
import BookTypeTable from "@/components/BookType/BookTypeTable";
import { mapActions, mapGetters } from "vuex";
import ClientTable from "@/components/Clients/ClientTable";
import ClientForm from "@/components/Clients/ClientForm";
import JournalForm from "@/components/Journal/JournalForm";
import JournalTable from "@/components/Journal/JournalTable";

export default {
  name: "JournalDisplayer",
  components: {
    JournalTable,
    JournalForm

  },
  computed: {
    ...mapGetters('journals', ['journals']),
    ...mapGetters('clients', ['clients']),
    ...mapGetters('books', ['books']),
  },
  methods: {
    ...mapActions('journals', ['addJournal', 'putJournal', 'deleteJournal']),
    onDelete(id) {
      this.$confirm('Журнал будет удален навсегда. Продолжить?', 'Предупреждение', {
        confirmButtonText: 'Ок',
        cancelButtonText: 'Отменить',
        type: 'warning'
      }).then(async () => {
        try {
          await this.deleteJournal(id);
          this.$message({
            type: 'success',
            message: 'Журнал успешно удален'
          });
        } catch (e) {
          this.$message({
            type: 'error',
            message: 'Неудачная попытка удаления'
          })
        }
      }).catch(() => {
      });
    },
    async onAdd(journal) {
      try {
        await this.addJournal(journal);
        this.$message({
          type: 'success',
          message: 'Журнал успешно добавлен'
        })
      } catch (e) {
        this.$message({
          type: 'error',
          message: 'Неудачная попытка добавления'
        })
      }
    },
    async onChange(journal) {
      try {
        await this.putJournal({ id: journal.id, journal });
        this.$message({
          type: 'success',
          message: 'Журнал успешно изменен'
        })
      } catch (e) {
        this.$message({
          type: 'error',
          message: 'Неудачная попытка изменения'
        })
      }
    }
  }
}
</script>

