<template>
<el-tabs type="border-card" class="center">
    <el-tab-pane label="Список">
        <template v-if="this.bookTypes.length">
            <BookTypeTable :bookTypeList="this.bookTypes" @handleDelete="onDelete" @handleChange="onChange"/>
        </template>
        <template v-else>
            Нет данных
        </template>
    </el-tab-pane>
    <el-tab-pane label="Добавить">
        <BookTypeForm class="formPadding" @handleSubmit="onAdd"/>
    </el-tab-pane>
</el-tabs>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import BookTypeTable from "@/components/BookType/BookTypeTable";
import BookTypeForm from "@/components/BookType/BookTypeForm";

export default {
  name: "BookTypeDisplayer",
  components: { BookTypeForm, BookTypeTable },
  computed: {
    ...mapGetters('bookTypes', ['bookTypes'])
  },
  methods: {
    ...mapActions('bookTypes', ['deleteBookType', 'addBookType', 'getBookTypeById', 'putBookType']),
    ...mapActions('books', ['fetchBooks']),
    onDelete(id) {
      this.$confirm('Тип книги будет удален навсегда. Продолжить?', 'Предупреждение', {
        confirmButtonText: 'Ок',
        cancelButtonText: 'Отменить',
        type: 'warning'
      }).then(async () => {
        try {
          await this.deleteBookType(id);
          this.$message({
            type: 'success',
            message: 'Тип книги успешно удален'
          });
        } catch (e) {
          this.$message({
            type: 'error',
            message: 'Неудачная попытка удаления'
          })
        }
      }).catch(() => {
      });
    },
    async onAdd(bookType) {
      try {
        await this.addBookType(bookType);
        this.$message({
          type: 'success',
          message: 'Тип книги успешно добавлен'
        })
      } catch (e) {
        this.$message({
          type: 'error',
          message: 'Неудачная попытка добавления'
        })
      }
    },
    async onChange(bookType) {
      try {
        await this.putBookType({ id: bookType.id, bookType });
        await this.fetchBooks();
        this.$message({
          type: 'success',
          message: 'Тип книги успешно изменен'
        })
      } catch (e) {
        this.$message({
          type: 'error',
          message: 'Неудачная попытка изменения'
        })
      }
    }
  }
}
</script>

