import bookTypeApiService from "@/services/bookTypeApiService";
import mutations from "@/store/mutations";
import Vue from "vue";
import { arrayToObj } from "@/helpers/storeHelpers";

const { SET, SET_ONE, DELETE } = mutations;

const bookTypeStore = {
  namespaced: true,
  state: {
    bookTypes: {}
  },
  getters: {
    bookTypes(state) {
      return Object.values(state.bookTypes);
    }
  },
  mutations: {
    [SET](state, objList) {
      state.bookTypes = objList;
    },
    [SET_ONE](state, obj) {
      Vue.set(state.bookTypes, obj.id, obj);
    },
    [DELETE](state, id) {
      Vue.delete(state.bookTypes, id);
    }
  },
  actions: {
    async fetchBookTypes({ commit }) {
      try {
        const response = await bookTypeApiService.getAll();

        commit(SET, arrayToObj(response));
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async addBookType({ commit }, bookType) {
      try {
        const response = await bookTypeApiService.post(bookType);

        commit(SET_ONE, response);
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async putBookType({ commit }, { id, bookType }) {
      try {
        const response = await bookTypeApiService.put(id, bookType);

        commit(SET_ONE, response);
      } catch (e) {
        return Promise.reject(e)
      }
    },
    async deleteBookType({ commit }, id) {
      try {
        await bookTypeApiService.delete(id);

        commit(DELETE, id);
      } catch (e) {
        return Promise.reject(e)
      }
    },
    getBookTypeById({ state }, id) {
      return state.bookTypes[id];
    }
  },
};

export default bookTypeStore;
