import axios from '../plugins/axios';

class JournalApiService {
  async getAll() {
    try {
      const result = await axios.get("/api/journals/all");

      return result.data;
    } catch (e) {
      return Promise.reject(e);
    }
  }

  async post(body) {
    try {
      const result = await axios.post(`/api/journals/add`, body);

      return result.data
    } catch (e) {
      return Promise.reject(e);
    }
  }

  async put(id, body) {
    try {
      const result = await axios.put(`/api/journals/${ id }`, body);

      return result.data;
    } catch (e) {
      return Promise.reject(e);
    }
  }

  async delete(id) {
    try {
      await axios.delete(`/api/journals/${ id }`);

    } catch (e) {
      return Promise.reject(e);
    }
  }
}

const journalApiService = new JournalApiService();

export default journalApiService;
