<template>
    <div id="app">
        <AuthButtons :authenticated="authenticated" class="authPosition"/>
        <Navbar v-if="authenticated"/>
        <router-view>
        </router-view>
    </div>
</template>

<script>
import Navbar from "@/components/Navbar";
import { mapActions } from 'vuex';
import BookTypeDisplayer from "@/components/BookType/BookTypeDisplayer";
import BookDisplayer from "@/components/Book/BookDisplayer";
import { isAuthenticated } from "@/helpers/authHelpers";
import CONST_ENV from "@/config/const.config";
import AuthButtons from "@/components/Auth/AuthButtons";

export default {
  name: 'App',
  components: {
    AuthButtons,
    BookDisplayer,
    BookTypeDisplayer,
    Navbar
  },
  computed: {
    authenticated() {
      return isAuthenticated();
    }
  },
  async mounted() {
    if (isAuthenticated()) {
      try {
        await Promise.all([
          this.fetchBooks(),
          this.fetchBookTypes(),
          this.fetchClients(),
          this.fetchJournals()
        ]);
      } catch (e) {
        this.$message({
          type: 'error',
          message: 'Авторизационные данные устарели'
        });
        setTimeout(() => {
          localStorage.clear();
          location.pathname = '/'
        }, 2000);
      }
    }
  },
  methods: {
    ...mapActions('books', ['fetchBooks']),
    ...mapActions('bookTypes', ['fetchBookTypes']),
    ...mapActions('clients', ['fetchClients']),
    ...mapActions('journals', ['fetchJournals']),
  }
}
</script>

